import { Component, ViewChild } from '@angular/core';
import { HostListener } from '@angular/core';

import { Platform, LoadingController } from '@ionic/angular';
import { SplashScreen } from '@ionic-native/splash-screen/ngx';
import { StatusBar } from '@ionic-native/status-bar/ngx';
import { ConnectionStatus, Global } from './services/global';
import { TranslateService } from '@ngx-translate/core';
import { AuthService } from './services/auth.service';
import { Router } from '@angular/router';
import { PrinterService } from './services/printer.service';
import { OrdersService } from './services/orders.service';
import { SuccessComponent } from './components/success/success.component';
import { ErrorComponent } from './components/error/error.component';
import { ConnectionService } from 'ng-connection-service';

@Component({
  selector: 'app-root',
  templateUrl: 'app.component.html',
  styleUrls: ['app.component.scss'],
})
export class AppComponent {

  @ViewChild("success", { static: false }) success_component: SuccessComponent;
  @ViewChild("error", { static: false }) error_component: ErrorComponent;
  public errorMessage: string;

  constructor(
    private platform: Platform,
    private splashScreen: SplashScreen,
    private statusBar: StatusBar,
    public translate: TranslateService,
    public auth: AuthService,
    public router: Router,
    public printerService: PrinterService,
    private ordersService: OrdersService,
    private loadingCtrl: LoadingController,
    private connectionService: ConnectionService
  ) {

    document.body.setAttribute('data-theme', 'dark');
    this.translate.use(Global.DEFAULT_LANG || 'it');
    this.translate.setDefaultLang('it');

    this.auth.networkLogin()
      .subscribe(res => {
      }, err => {
        console.log("ERROR AUTH", err)
      })

    this.connectionService.monitor()
      .subscribe(isConnected => {
        Global.CONNECTION_STATUS = isConnected ? ConnectionStatus.ONLINE : ConnectionStatus.OFFLINE;
        if (isConnected) {
          this.error_component.hide();
        } else {
          this.error_component.show(0, "Errors._NO_INTERNET_CONNECTION");
        }

        console.log(Global.getDeviceStatus(false));
      });

    Global.validationSuccess.subscribe(res => {
      this.success_component.show();
      setTimeout(() => {
        this.success_component.hide()
      }, 3000)
    });

  }

  async initializeApp() {

    await this.platform.ready()
    this.statusBar.styleDefault();
    this.splashScreen.hide();
  }

  ionViewDidEnter() {

  }


  private scan_text: string = '';
  @HostListener('document:keypress', ['$event'])
  async handleKeyboardEvent(event: KeyboardEvent) {
    if (event.key == 'Enter') {
      try {
        let data = JSON.parse(atob(this.scan_text));
        console.log(data)
        let id = data.id;
        if (data.confirm) {
          this.router.navigateByUrl('/confirm-order/' + id)
        } else {
          this.validate(id);
        }

        this.scan_text = '';
      } catch (error) {
        console.log(error)
      }
    }
    this.scan_text += event.key;
    setTimeout(() => {
      this.scan_text = '';
    }, 1000)
  }


  async validate(code: string) {
    if (Global.CONNECTION_STATUS == ConnectionStatus.OFFLINE) {
      this.error_component.show(0, "Errors._NO_INTERNET_CONNECTION");
      return;
    }
    let loading = await this.loadingCtrl.create({ message: "loading..." });
    loading.present();

    let canPrint = await this.printerService.canPrint();
    if (!canPrint) {
      let err = { error: { code: "_CANT_PRINT" } };
      Global.validationError.emit(err);
      this.errorMessage = err.error.code;

      await loading.dismiss();

      this.error_component.show();
      setTimeout(() => {
        this.error_component.hide()
      }, 3000)
      return;
    }

    this.ordersService.validateAndPrint(code)
      .subscribe(res => {
        loading.dismiss();
        this.success_component.show();
        setTimeout(() => {
          this.success_component.hide()
        }, 3000)
        console.log(res);
      }, async err => {
        loading.dismiss();
        console.log("PRINTER ERROR", err);
        Global.validationError.emit(err);
        this.errorMessage = err.error.code;

        await loading.dismiss();

        this.error_component.show();
        setTimeout(() => {
          this.error_component.hide()
        }, 3000)
      });

    // this.ordersService.validateOrderByCode(code)
    //   .subscribe(async (res: any) => {
    //     if (res) {
    //       await loading.dismiss();

    //       if (!res) return;

    //       // PRINT 
    //       this.printerService.printOrder(res)
    //         .then(res => {
    //           loading.dismiss();
    //           this.success_component.show();
    //           setTimeout(() => {
    //             this.success_component.hide()
    //           }, 3000)
    //           console.log(res);
    //         }).catch(err => {
    //           loading.dismiss();
    //           console.log("PRINTER ERROR", err)
    //         })
    //     }
    //   }, async err => {
    //     Global.validationError.emit(err);
    //     this.errorMessage = err.error.code;

    //     await loading.dismiss();

    //     this.error_component.show();
    //     setTimeout(() => {
    //       this.error_component.hide()
    //     }, 3000)
    //   })

  }

}
