import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Global } from './global';
import { map, catchError } from "rxjs/operators";
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class OrdersService {

  constructor(private http: HttpClient) { }

  findById(id: number) {
    return this.http.get(Global.ENDPOINTS.ORDERS + "/orders/" + id, { params: { filter: "{}" } });
  }

  validateAndPrint(idOrCode: string) {
    return this.http.post(`${Global.ENDPOINTS.PRINTER}/orders/${idOrCode}/validate`, { id: idOrCode }, { headers: { lang: Global.DEFAULT_LANG } });
  }

  validateOrder(order: any) {
    return this.http.put(Global.ENDPOINTS.ORDERS + "/orders/" + order.id + "?updateFields=validated&includes=event", { validated: new Date() })
      .pipe(
        map((res: any) => {
          //FileManager.newLineAppendTo(Electron.getGlobal('__dirname') + `/${order.fk_event}/orders/validations.txt`, res.id).then(res => { }, )
          return res;
        }),
        catchError(async x => {
          try {
            //let result = await FileManager.newLineAppendTo(Electron.getGlobal('__dirname') + `/${order.fk_event}/orders/validations.txt` , "" + order.id + "#" + Date.now())
          } catch (error) {
            return Observable.throw(error);
          }
          return order;
        })
      );
  }

  validateOrderByCode(code: string) {
    return this.http.get(Global.ENDPOINTS.ORDERS + "/orders/" + code + "/validate?updateFields=validated");
  }

}
