import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { CURRENCY, CurrencySymbolPipe, } from '../pipes/currency-symbol.pipe';
import { I18nDatePipe } from '../pipes/i18n-date.pipe';
import { Global } from './global';
import { Totem } from "../models/totem.model";

@Injectable({
  providedIn: 'root'
})
export class PrinterService {

  constructor(private translate: TranslateService, private http: HttpClient) { }

  async printOrder(order: any) {

    let i18nDatePipe = new I18nDatePipe(this.translate);
    let currencySymbolPipe = new CurrencySymbolPipe();

    if (!order.movement || !order.movement.currency) {
      order.movement = { currency: 'eur' };
    }

    let current = 0;
    let total = 0;
    for (let r of order.rows) {
      total += r.qta;
    }

    let tickets = [];
    for (let r of order.rows) {
      console.log("print - " + r.name)
      for (let i = 0; i < r.qta; i++) {

        current++;

        let ticket = {
          date: i18nDatePipe.transform(order.event.from, 'EEE dd MMM'),
          location: order.event.location,
          event_title: order.event.title,
          product_name: r.name,
          price: r.price / Math.pow(10, CURRENCY[(order.movement.currency || 'none').toUpperCase()].decimal_digits || 2),
          currency: currencySymbolPipe.transform(order.movement.currency) || '€',
          printed: i18nDatePipe.transform(order.created, `\'${this.translate.instant("Printer.bought_at")}\'dd/MM/YY HH:mm`),
          watermark: order.watermark || "AddoAddoAddoAddo",
          rotateWatermark: true,
          info: `Voucher valido per il ritiro al bancone. Recati dal barman per ordinare l'articolo acquistato. Coupon valido fino al ${i18nDatePipe.transform(order.event.to, 'dd/MM/YY \'alle\' HH:mm')}. Una volta stampato il vaucher non potrà essere più rimborsato.`
        };

        tickets.push({ ticket, clear: current == total });
      }
    }

    return await this.http.post(`${Global.ENDPOINTS.PRINTER}/tickets/print`, tickets).toPromise();
  }

  async checkPrinterStatus(): Promise<Totem> {
    return await this.http.get<Totem>(`${Global.ENDPOINTS.PRINTER}/status`).toPromise();
  }

  async canPrint() {
    let check_status = await this.checkPrinterStatus();
    return check_status.status.filter(cs => cs != PrinterErrorStatus.DRAWER_OPENED && cs != PrinterErrorStatus.PAPER_TAKEOUT).length == 0;
  }

}

export enum PrinterErrorStatus {
  DRAWER_OPENED = 'DRAWER_OPENED',
  OFFLINE = "OFFLINE",
  COVER_UP = "COVER_UP",
  FEED_PRESSED = "FEED_PRESSED",
  NO_PAPER = "NO_PAPER",
  ERROR_OCCURIED = "ERROR_OCCURIED",
  CUTTER_ERROR = "CUTTER_ERROR",
  UNRECOVERABLE_ERROR = "UNRECOVERABLE_ERROR",
  DEGREE_OR_VOLTAGE_OVERRANGE = "DEGREE_OR_VOLTAGE_OVERRANGE",
  PAPER_NEAREND = "PAPER_NEAREND",
  PAPER_TAKEOUT = "PAPER_TAKEOUT",
}
