import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

declare var confetti: any;

@Component({
  selector: 'success-effect',
  templateUrl: './success.component.html',
  styleUrls: ['./success.component.scss'],
})
export class SuccessComponent implements OnInit {

  @Input('options') options: { showOnStart: boolean, delay?: number, cashback?: { rewards: number, confetti: { duration: number } } } = { showOnStart: false, delay: 0 };
  @Input('successText') successText: string;
  @Input('text') text: string;

  @Output("callback") callback: EventEmitter<any> = new EventEmitter<any>()

  public classes: any = {}
  public isShow: boolean = false

  constructor() {

  }

  ngOnInit(): void {
    if (this.options.showOnStart) {
      this.show(this.options.delay);
    }
  }

  show(time: number = 0) {
    setTimeout(() => {
      this.isShow = true
      if (this.options.cashback) {
        confetti.start();
        setTimeout(()=>{
          confetti.stop();
        }, this.options.cashback.confetti.duration || 500)
      }
    }, time || 0)
  }

  hide(time: number = 0) {
    setTimeout(() => {
      this.isShow = false;
      confetti.stop();
    }, time || 0)
  }

}
