import { JwtHelperService } from "@auth0/angular-jwt";
import { EventEmitter } from '@angular/core';


export const enum ConnectionStatus {
    ONLINE,
    OFFLINE,
}

export const enum QrCodeStatus {
    ON
}

export const enum PrinterStatus {
    ON
}

export const enum TouchScreenStatus {
    ON
}



export class Global {

    public static CREDENTIALS = {
        username: "device",
        password: "123456"
    }

    public static ENDPOINTS = {
        ORDERS: "https://i8jrtirjm3.execute-api.eu-west-3.amazonaws.com/dev",
        USERS: "https://t2zzcvkhjb.execute-api.eu-west-3.amazonaws.com/dev",
        PRINTER: "http://localhost:2458",
        TOTEMS: "https://7ifca48yhf.execute-api.eu-west-3.amazonaws.com/dev"
    }
    public static jwt_helper = new JwtHelperService();

    public static validationSuccess: EventEmitter<any> = new EventEmitter<any>();
    public static validationError: EventEmitter<any> = new EventEmitter<any>();

    public static CONNECTION_STATUS: ConnectionStatus = ConnectionStatus.ONLINE;
    public static PRINTER_STATUS: PrinterStatus = PrinterStatus.ON;
    public static TOUCHSCREEN_STATUS: TouchScreenStatus = TouchScreenStatus.ON;
    public static QRCODE_STATUS: QrCodeStatus = QrCodeStatus.ON;

    public static DEFAULT_LANG = 'it';

    public static getDeviceStatus(short_result: boolean = true) {
        let result_short = {
            c: { s: Global.CONNECTION_STATUS },
            p: { s: Global.PRINTER_STATUS },
            qr: { s: Global.QRCODE_STATUS },
            ts: { s: Global.TOUCHSCREEN_STATUS },
        }
        let result = {
            connection: { status: Global.CONNECTION_STATUS },
            printer: { status: Global.PRINTER_STATUS },
            qrcode: { status: Global.QRCODE_STATUS },
            touchscreen: { status: Global.TOUCHSCREEN_STATUS },
        }
        return short_result ? result_short : result;
    }
}
