import { Injectable } from '@angular/core';
import { Global } from './global';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class AuthService {

  public token: string;
  public identity: any;
  public logged: boolean = false;

  constructor(private http: HttpClient) { }

  public getToken(): string {
    return localStorage.getItem('token') || this.token;
  }

  public setToken(token: string) {
    if (!token) {
      localStorage.removeItem('token');
    } else {
      localStorage.setItem('token', token);
    }

    this.token = token;
  }

  public isAuthenticated(): boolean {
    const token = this.getToken();
    let result = false;

    try {
      result = Global.jwt_helper.decodeToken(token)
    } catch (error) {
    }

    return result;
  }

  public setIdentity(user: any) {
    this.identity = user;
  }

  logout() {
    this.setToken(null);
    this.setIdentity(null);
  }

  networkLogin() {
    let body = { 
      username: Global.CREDENTIALS.username, 
      password: Global.CREDENTIALS.password, 
      provider: 'native', 
      access_token: null 
    };

    return (this.http.post(Global.ENDPOINTS.USERS + "/login", body) as Observable<{ user: any, token: string }>)
      .pipe(map(x => {
        this.setToken(x.token);
        this.setIdentity(x.user);
        return x;
      }));
  }

  
}
