import { DatePipe } from '@angular/common';
import { Pipe, PipeTransform } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';

@Pipe({
  name: 'i18nDate'
})
export class I18nDatePipe implements PipeTransform {

  constructor(private translateservice: TranslateService) {}

  transform(value: any, format?: string, timeZome?: string): any {
    const datePipe = new DatePipe(this.translateservice.currentLang);
    return datePipe.transform(value, format, timeZome);
  }

}
