import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'error-effect',
  templateUrl: './error.component.html',
  styleUrls: ['./error.component.scss'],
})
export class ErrorComponent implements OnInit {

  @Input('options') options: { showOnStart: boolean, delay?: number, cashback?: { rewards: number, confetti: { duration: number } } } = { showOnStart: false, delay: 0 };
  @Input('successText') successText: string;
  @Input('text') text: string;

  @Output("callback") callback: EventEmitter<any> = new EventEmitter<any>()

  public classes: any = {}
  public isShow: boolean = false

  constructor() {

  }

  ngOnInit(): void {
    if (this.options.showOnStart) {
      this.show(this.options.delay);
    }
  }

  show(time: number = 0, text: string = null) {
    this.text = text || this.text;
    setTimeout(() => {
      this.isShow = true
    }, time || 0)
  }

  hide(time: number = 0) {
    setTimeout(() => {
      this.isShow = false;
      this.text = '';
    }, time || 0)
  }

}
